.NavBar{
    bottom: -62px;
    position: absolute;
    background-color: none;
    height: 6vh;
    align-items: center;
    display: flex;
    z-index: 100;
    background-color: #000000e6;
    transition: opacity 0.5s;
    opacity: 1;
    width: 100%;
}

.NavBar-logo{
    flex:2
}
.NavBar a{
    font-size: 1.1rem;
    font-weight: bold;
    text-decoration: none;
    color: rgb(255, 255, 255);
    margin-left: 15px;
    margin-right: 15px;
    display: inherit;
    transition: display 1.2s;
}
.NavBar a:hover{
    cursor: pointer;
    color: #ff00cf;
}
.NavBar> .active{
    color: #ff00cf;
}
.NavBar.active{
    position: fixed;
    bottom: auto;
    opacity: 1;
}



  @media only screen and (min-width: 1110px) {
    .NavBar{
        background-color: #00000000;
        opacity: 1;
        width: 30%;
        bottom: -43px;

    }
    .NavBar a{
        color: black;
    }
  }