*{
    margin: 0;
    
}
body{
    background: linear-gradient(-45deg, #E91E63, #2d111e, #251f48, #920787);
    background-size: 400% 400%;
    animation: gradient 15s ease infinite;
    background-attachment: fixed;
    font-family: 'Raleway', sans-serif;

}
.container{
    display: flex;
    min-height: 100vh;
}
.flex-column{
    flex-direction: column;
    align-items: center;
}
.flex-row{
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
}
h2{
    font-family: 'Raleway', sans-serif;
    color: #ba02a3;
    font-size: 3rem;

}
h3{
    font-family: 'Raleway', sans-serif;
    color: #ba02a3;
    font-size: 2rem;
    margin-bottom: 2rem;

}
h4{
    font-family: 'Raleway', sans-serif;
    color: #ba02a3;
    font-size: 20px;
}
.ns{
    -moz-user-select: none; 
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none; 
}
.title{
    margin-top: 5rem;
    margin-bottom: 5rem;
    display: flex;
    flex-direction: column;
}
.title-line{
    border-bottom: solid #828282 3px;
    margin-top: 7px;
    width: 0%;
    transition: width 1s;

}
.title-line.active{
    width: 67%;
}
@keyframes gradient {
    0% {
        background-position: 0% 50%;
    }

    50% {
        background-position: 100% 50%;
    }

    100% {
        background-position: 0% 50%;
    }
}