.Card{
    width: 300px;
    height: 208px;  
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
}
.Card img{
    z-index: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: height 1s;

}

.Card:hover > img{
    height: 0%;
}

.Card-info{
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;

    padding: 40px;

    z-index: 10;
    font-weight: bold;
    color:rgb(5, 5, 5);
    position: absolute;
    transition: filter 1s;
    transition-delay: 0s;
}   
.Card-title{
    font-size: 28px;
    margin-bottom: 6px;
}
.Card-subtitle{
    font-size: 15px;
    color: #c31aa3;
}
.Card:hover > .Card-info > .Card-button{
    margin-top: 0;
    
}
.Card-button{
    margin-top: 202px;
    transition: margin-top 1s;
}

.Card svg{
    margin: 1rem;
    font-size: 2rem;
}
.Card svg:hover{
    cursor: pointer;
    color: #c31aa3;
}
.Card:hover{
    color: white;
}
