.profileImg{
    width: 293px;
    margin-right: 40px;
    flex: 1;
}
.About-page{
    height: 100vh;
    overflow: hidden;
}
.About-section{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 63vh;
}
.About-me{
    font-size: 1.8rem;
    width: 72%;
}
.About-section-2{
    grid-gap: 1rem;
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    padding-bottom: 7rem;
    justify-content: center;
    align-items: center;
    font-size: 1.5rem;
}
.About-section-2 > div{
    text-align: center;
}
.About-section-2 svg{
    font-size: 4rem;
}
.About-grid{
    flex: 1;
    height: 36vh;
}
.About-Tech{
    grid-template-columns: repeat(3, 1fr);
    display: grid;
    grid-gap: 3rem 0rem;
}
.About-ss{
    flex: 1;
    height: 36vh;
}
.About-st{
    flex: 2;
}
.About-services{
    justify-content: center;
    align-items: center;
    grid-template-columns: repeat(3, 1fr);
    display: grid;
    font-size: 1.3rem;
    grid-gap: 3rem 2rem;
    margin-left: 20px;
    margin-right: 20px;

}
.About-Button-More{
    background-color: #54163e;
    margin: 5rem;
    border: 1px solid white;
    border-radius: 8px;

}
.About-Button-More:focus{
    outline: none;
}
.About-Button-More:hover{
    cursor: pointer;

}
.About-Button-More svg{
    font-size: 3rem;
}
.About-Button-More:hover > .ArrowMoreIcon{
    animation: arrow 1.4s ease infinite;

}

.ArrowMoreIcon{
    display: inline-block;
    color: white;
    transform:translateY(0px);
    transition: transform 0.5s;
    padding: 0.3rem;
}
.About{
    max-height: 100vh;
    overflow-y: hidden;
    transition: max-height 0.5s linear;

}

.About.open{
    max-height: 216vh;
}
.container .about-div{
    background: white;
    height: 100vh;
    width: 100%;
}
@keyframes arrow {
    0% {
        transform:translateY(0px);
    }
    50% {
        transform:translateY(10px);
    }
    100% {
        transform:translateY(0px);
    }
}

/* responsive */
@media only screen and (min-width: 1110px) {
    .About-section{
        flex-flow: nowrap;
    }
    .About-section-2{
        place-items: center;
        grid-gap: 6rem;
    }
    .About-me{
        font-size: 1.5rem;
        width: 40%;
        }
        .About-st{
            align-self: center;
            width: 26vw;
        }
        .About-services > div {
            margin-left: 5rem;
            margin-right: 5rem;
        }
    .NavBar a{
        color: black;
    }
  }