.Footer{
    position: relative;
    display: flex;
    background: #00000087;
    height: 20vh;
    justify-content: center;
    align-items: center;
}
.upButton{
    color: white;
    font-size: 20px;
    position: absolute;
    top: -29px;
    background: #0808088c;
    border-radius: 14px;
    padding: 20px;
    right: 13vh;
    transition: background-color 1s;
}
.upButton:hover{
    cursor: pointer;
    background-color: black;
}
.upButton:hover > svg{
    animation: arrowUp 2s ease infinite;
}
.Footer a{
    padding: 0.7rem 0.7rem 0rem 0.7rem;
    background-color: #7373733b;
    font-size: 2.5rem;
    color: #ffffff;
    margin: .5rem;
    border-radius: 9px;
}

.Footer a:hover > svg{
    animation: iconAnimation 2s ease;
}
.footer-content{
    display: flex;
    flex-direction: column;
    /* justify-content: center; */
    align-items: center;
}
.Footer-info{
    color: white;
    margin-top: 1rem;
    font-size: 0.6rem;
}
@keyframes arrowUp {
    0% {
        transform:translateY(0px);
    }

    50% {
        transform:translateY(-10px);
    }
    100% {
        transform:translateY(0px);
    }
}
@keyframes iconAnimation {
    0% {
        transform:translateY(0px);
    }
    25%{
        transform:translateY(-10px);

    }
    50% {
        transform:translateY(10px);
    }
    100% {
        transform:translateY(0px);
    }
}